// External
import React, { useState, useEffect } from 'react';

// Material UI
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

// Owned
import { useStyles } from '../styles';
import HorizontalLogo from '../HorizontalLogo';
import { Request, Messages } from '../../../../shared';

import { useTranslation } from 'react-i18next';

export default function Welcome() {
    const classes = useStyles();
    const { t } = useTranslation();

    const [loading, setLoading] = useState(false);

    useEffect(() => {
        setLoading(false);
    }, []);

    return(
        <Container component="main" maxWidth="md">
            <CssBaseline />
            {loading ?
                <div className={classes.paper}>
                    <CircularProgress />
                </div>
            :
                <div className={classes.welcomeHeader}>
                    <div className={classes.imgHeader}>
                        <HorizontalLogo light={true} width={"300px"} />
                    </div>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <Box>
                                <Typography variant="body" sx={{
                                    color: "lightGrey",
                                    marginTop: "20px"
                                }}>
                                    Hi there! 
                                    <br />
                                    <br />
                                    Thank you for using Super Agent. We're glad you're finding it helpful! 
                                    In order to keep Super Agent up and running smoothly, we're introducing a small subscription fee of $1.19 per month or 
                                    $11.99 a year for iOS devices. We'll soon be adding a lifetime access purchase of $29.99. 

                                    <br/>
                                    <br/>

                                    <b>Super Agent will still work up to 3 times a day in the free version.</b>
                                    

                                    <br />
                                    <br />
                                    You can subscribe by opening the Super Agent app, going to Settings and Manage Subscriptions.
                                    <br />
                                    <br />
                                    
                                    We won't bother you with this message again for a while, 
                                    but we wanted to let you know that we really appreciate your support as a user of Super Agent. 
                                    If you do decide to subscribe, you'll get unlimited access to all of Super Agent's present and future features. 
                                    If not, no hard feelings! 
                                    
                                    <br />
                                    <br />
                                    
                                    Thank you for being a part of the Super Agent community.
                                </Typography>
                            </Box>
                        </Grid>
                        <Grid item xs={12}>
                            <Box>
                                <Button 
                                    variant="contained"
                                    color="secondary"
                                    onClick={async () => {
                                        await new Request(Messages.Generic.AppOpen, {}).sendRequest();
                                        window.location.replace("superagent:subscribe");
                                    }}
                                >
                                    Subscribe Now
                                </Button>
                            </Box>
                        </Grid>
                    </Grid>
                </div>            
            }

        </Container>
    )
}
